* {
margin:0;
padding:0;
}

.App {
  text-align: center;
  margin:0;
  padding: 20px;
}

.Nav-header {
  background-color: #222;
  height: 200px;
  margin-top: -20px;
  color: white;
  background-image: url('../Assets/crystaldroplets-web.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 54%;
  text-align: center; /*why doesn't name line up with the rest?*/
  vertical-align: middle;
}

.Title {
  padding: 25px;
}

.App-intro {
  font-size: large;
}

.Nav-list {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

li {
  margin-right: 3%;
  margin-left: 3%;
}

/*Link styles --------------------------------------------------- */

a.nav:link {
  float: left;
  display:block;
  color: white;
  text-decoration: none;
  font-weight: 600;
  margin-top: 50px;
  margin-bottom: 50px;
}

a.nav:visited { color: white; }

a.nav:active {
  -webkit-transform: scale(.8);
      -ms-transform: scale(.8);
          transform: scale(.8);
  -webkit-transition: 250ms ease 0s;
  -o-transition: 250ms ease 0s;
  transition: 250ms ease 0s;
}

a.nav:hover { 
  text-shadow: 0px 0px 5px white, 0px 0px 20px white;
  -webkit-transition: 250ms linear 0s;
  -o-transition: 250ms linear 0s;
  transition: 250ms linear 0s;
}

p {
  max-width: 800px;
  margin:0 auto;
}

.selected {
  border-bottom: 2px solid white;
}

.Title {
  font-size: 49px;
  text-shadow: 0px 0px 25px black;
}

.transition-anim {
  width: calc(100% - 20px);
  height: 100%;
  margin: 10px;
}

.transition-block{
  position: absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
}

.temp {
  position: relative;
}

.profilePic {
  text-size: 16px;
  border-radius: 50%;
}

.object {
  height: 100%;
  width: 100%;
}


